import { Component, ContentChild, Directive, EventEmitter, Input, Output, TemplateRef } from '@angular/core';
import { ApprovalLevelStatus, DataGroup } from '@backbase/accessgroup-common-ang';
import { BehaviorSubject, combineLatest, Observable } from 'rxjs';
import { GetUser as User } from '@backbase/data-ang/user';
import { map } from 'rxjs/operators';
import {
  BusinessFunction,
  BusinessFunctionPrivileges,
  DataGroupWithPayee,
  SelfApprovalDefaultBehavior,
  SelfApprovalPolicies,
  UserPermissions,
} from '../model/types';

@Directive({
  selector: 'ng-template[bbUserPermissionsItemBusinessFunction]',
})
export class UserPermissionsItemBusinessFunctionDirective {
  constructor(public templateRef: TemplateRef<any>) {}
}
@Directive({
  selector: 'ng-template[bbUserPermissionsItemBusinessFunctionGroupName]',
})
export class UserPermissionsItemBusinessFunctionGroupNameDirective {
  constructor(public templateRef: TemplateRef<any>) {}
}

@Component({
  selector: 'bb-user-permissions-item',
  templateUrl: './user-permissions-item.component.html',
})
export class UserPermissionsItemComponent {
  @ContentChild(UserPermissionsItemBusinessFunctionDirective)
  businessFunctionTemplate: UserPermissionsItemBusinessFunctionDirective | undefined;
  @ContentChild(UserPermissionsItemBusinessFunctionGroupNameDirective)
  businessFunctionGroupKeyTemplate: UserPermissionsItemBusinessFunctionGroupNameDirective | undefined;
  _userPermissions: UserPermissions | undefined;
  allLinkedPayee: any;
  dataGroups: DataGroupWithPayee[] = [];

  selectedDataGroups$: BehaviorSubject<any> = new BehaviorSubject([]);
  selectedDataGroupsForPayee$: BehaviorSubject<any> = new BehaviorSubject([]);
  selectedPayeeGroups$: BehaviorSubject<any> = new BehaviorSubject([]);
  readonly dataGroups$ = new BehaviorSubject([]);
  readonly payeeGroups$ = new BehaviorSubject([]);
  payeeDetailModal = false;
  accountDetailModal = false;

  selfApproveItemData: SelfApprovalPolicies | undefined = undefined;
  typeOfSelfApproveAction = '';
  selectedBFForSelfApprove: BusinessFunction | undefined = undefined;

  readonly approvalLevelStatus = ApprovalLevelStatus;

  /**
   * User info
   */
  @Input() user: User | undefined;

  /**
   * Currency info
   */
  @Input() currencyList: any | undefined;

  /**
   * selfApproveDefaultBehavior
   */
  @Input() selfApproveDefaultBehavior: SelfApprovalDefaultBehavior | undefined = 'disable';

  /**
   * Whether to show approval level
   */
  @Input() showApprovalLevel = false;

  /**
   * Approval level
   */
  @Input() approvalLevel?: string | ApprovalLevelStatus;

  /**
   * Approval level loading flag
   */
  @Input() approvalLevelLoading: boolean | undefined;

  /**
   * Approval level error flag
   */
  @Input() approvalLevelError: boolean | undefined;

  /**
   * Limit data
   */
  @Input() limitData: Array<any> | undefined;

  /**
   * Permission base on cxp and user permission
   */
  @Input() userLimitsPermission: any | undefined;

  /**
   * Business Function to check privileges against
   */
  @Output() emitLimitData = new EventEmitter();

  emitLimit(emitData: any, bfName: string, jrName: string) {
    this.emitLimitData.emit({
      ...emitData,
      bfName,
      jrName
    });
  }

  /**
   * Self approval data
   */
  selfApprovalPolicies: Array<SelfApprovalPolicies> | undefined;

  /**
   * Self approval data
   */
  selfApprovalPoliciesHistory: Array<SelfApprovalPolicies> | undefined;

  /**
   * User Permissions Object consisting of a
   * Function Group, Grouped Business Function, Data Groups
   */
  @Input() set userPermissions(val: UserPermissions | undefined) {
    if (val) {
      this._userPermissions = val;
      if (val.dataGroups) {
        this.dataGroups = JSON.parse(JSON.stringify(val.dataGroups));
        this.selfApprovalPolicies = val.selfApprovalPolicies;
        if (!this.selfApprovalPoliciesHistory) {
          this.selfApprovalPoliciesHistory = val.selfApprovalPolicies;
        }
        this.selectedDataGroups$.next(val.dataGroups);
        this.getAllLinkedPayees(val.dataGroups);
        this.emitRequestObject();
        this.preEditDataGroupState = JSON.parse(JSON.stringify(this.dataGroups));
      }
    }
  }

  /**
   * Account Group setter
   */
  @Input() set dataGroup(val: any) {
    if (val) {
      this.dataGroups$.next(val);
    }
  }

  /**
   * Payee Group setter
   */
  @Input() set payeeGroups(val: any) {
    if (val) {
      this.payeeGroups$.next(val);
    }
  }
  /**
   * Whether to show unassign buttons
   */
  @Input() showActions = false;

  /**
   * Whether to show self approval section
   */
  @Input() enableSelfApproval = false;

  /**
   * Whether to show payee table row
   */
  @Input() showPayee = true;
  /**
   * Whether to show unassign buttons
   */
  @Input() showFunctionGroupType = false;
  /**
   * Whether to disable save button
   */
  @Input() disableSaveButton = true;
  /**
   * Flag to indicate edit view
   */
  @Input() editView = false;

  /**
   * Legal Entity Id
   */
  @Input() legalEntityId = '';

  @Input() editedFunctionId: string | undefined = '';
  /**
   * Event emitter emiting on unassigning function group
   */
  @Output() removeFunctionGroup = new EventEmitter<void>();
  /**
   * Event emitter emiting on unassigning data group
   */
  @Output() removeDataGroup = new EventEmitter<DataGroup>();

  @Output() requestObjectEmit = new EventEmitter();

  @Output() editedFunctionIdEmit = new EventEmitter();
  @Output() changesMadedEmit = new EventEmitter();

  readonly availableDataGroupsSearchTerm$ = new BehaviorSubject<string>('');
  readonly availableDataGroupsForPayeesSearchTerm$ = new BehaviorSubject<string>('');
  readonly availablePayeeGroupsSearchTerm$ = new BehaviorSubject<string>('');
  /**
   * Switcher state data
   * @private
   */
  private defaultSwitcherData = false;

  /**
   * Switcher value for account
   */
  switchAccountType$ = new BehaviorSubject<boolean>(false);

  readonly splittedAccountsByType$ = combineLatest([this.selectedDataGroups$, this.dataGroups$]).pipe(
    map(([selectedDataGroups, dataGroups]: [DataGroup[], DataGroup[] | undefined]) => {
      const wizardAccounts: DataGroup[] = [];
      const customAccounts: DataGroup[] = [];
      if (dataGroups) {
        const allDataGroups = dataGroups.filter((item: DataGroup) => !selectedDataGroups.find(o => o.id === item.id));
        allDataGroups.forEach((item: any) => {
          if (item.metadata?.generated) {
            wizardAccounts.push(Object.assign({ ...item }));
          } else {
            customAccounts.push(Object.assign({ ...item }));
          }
        });
      }
      return { allAccounts: [...customAccounts, ...wizardAccounts], wizardAccounts, customAccounts };
    }),
  );

  readonly availableDataGroups: Observable<DataGroup[]> = combineLatest([
    this.splittedAccountsByType$,
    this.switchAccountType$,
    this.availableDataGroupsSearchTerm$,
  ]).pipe(
    map(([splittedAccounts, switcherType, query]) => {
      let accounts = [];
      if (!switcherType && splittedAccounts.customAccounts.length) {
        accounts = splittedAccounts.customAccounts;
      } else {
        accounts = splittedAccounts.allAccounts;
      }
      accounts = accounts.filter(f => f.name.toLowerCase().includes(query.toLowerCase()));
      return accounts;
    }),
  );

  readonly availableDataGroupsForPayee: Observable<DataGroup[]> = combineLatest([
    this.selectedDataGroupsForPayee$,
    this.selectedDataGroups$,
    this.availableDataGroupsForPayeesSearchTerm$,
  ]).pipe(
    map(([selectedDataGroups, dataGroups, query]: [DataGroup[], DataGroup[] | undefined, string]) => {
      if (dataGroups) {
        return dataGroups
          .filter((item: DataGroup) => !selectedDataGroups.find(o => o.id === item.id))
          .filter(f => f.name.toLowerCase().includes(query.toLowerCase()));
      }
      return [];
    }),
  );

  readonly availablePayeeGroups: Observable<DataGroup[]> = combineLatest([
    this.selectedPayeeGroups$,
    this.payeeGroups$,
    this.availablePayeeGroupsSearchTerm$,
  ]).pipe(
    map(([selectedDataGroups, dataGroups, query]: [DataGroup[], DataGroup[] | undefined, string]) => {
      if (dataGroups) {
        return dataGroups
          .filter((item: DataGroup) => !selectedDataGroups.find(o => o.id === item.id))
          .filter(f => f.name.toLowerCase().includes(query.toLowerCase()));
      }
      return [];
    }),
  );

  payeeIds = undefined;
  accountIds = undefined;
  payeeGroupName = '';
  accountGroupName = '';

  selectAccountGroupsModalOpened = false;
  selectAccountGroupsForPayeesModalOpened = false;
  selectPayeeGroupsModalOpened = false;

  selectedForAssignAccountGroup!: DataGroupWithPayee | undefined;
  selectedForAssignAccountGroupForPayee!: DataGroupWithPayee | undefined;

  selectedDg: any;
  selectedPg: any;
  activeView = 'table';
  activeDataGroup = 'account';
  preEditDataGroupState!: DataGroupWithPayee[];
  objectForRequest: any;

  dataGroupForRemove: any;

  switchAccounts() {
    this.defaultSwitcherData = !this.defaultSwitcherData;
    this.switchAccountType$.next(this.defaultSwitcherData);
  }

  onRemoveFunctionGroup() {
    this.removeFunctionGroup.emit();
  }

  onRemoveDataGroup(dg: DataGroup) {
    this.removeDataGroup.emit(dg);
  }

  showLinkedPayee(dg: any) {
    this.selectedPg = undefined;
    if (this.selectedDg === dg) {
      this.selectedDg = undefined;
    } else {
      this.selectedDg = dg;
    }
  }

  selectedPG(pg: any) {
    if (this.selectedPg === pg) {
      this.selectedPg = undefined;
    } else {
      this.selectedPg = pg;
    }
  }

  checkInclude(payees: any) {
    if (payees && this.selectedPg) {
      return payees.filter((val: any) => {
        return val.id === this.selectedPg.id;
      }).length;
    }
    return false;
  }

  getAccountGroupLinked(payee: any) {
    const accountGroups: any = [];
    this.dataGroups.forEach((el: any) => {
      if (
        el['payees'] &&
        el['payees'].filter((val: any) => {
          return val.id === payee.id;
        }).length
      ) {
        accountGroups.push(el);
      }
    });
    return accountGroups;
  }

  getAccountGroupLinkedString(payee: any) {
    let accountGroups: any = '';
    this.dataGroups.forEach((el: any, idx: number, array: any) => {
      if (
        el['payees'] &&
        el['payees'].filter((val: any) => {
          return val.id === payee.id;
        }).length
      ) {
        accountGroups = accountGroups + "'" + el.name + "'";
        if (idx !== array.length - 1) {
          accountGroups = accountGroups + ', ';
        }
      }
    });
    return accountGroups;
  }

  assignDataGroups(val: DataGroupWithPayee[]) {
    val.forEach((item: DataGroupWithPayee) => {
      this.dataGroups.push(Object.assign({}, item));
    });
    this.selectedDataGroups$.next(this.dataGroups);
    this.emitRequestObject();
  }

  searchAvailableDataGroups(val: string) {
    this.availableDataGroupsSearchTerm$.next(val);
  }

  searchAvailablePayeeGroups(val: string) {
    this.availablePayeeGroupsSearchTerm$.next(val);
  }

  searchAvailableDataGroupsForPayees(val: string) {
    this.availableDataGroupsForPayeesSearchTerm$.next(val);
  }

  addAccountGroup() {
    this.defaultSwitcherData = false;
    this.switchAccountType$.next(false);
    this.availableDataGroupsSearchTerm$.next('');
    this.selectAccountGroupsModalOpened = true;
  }

  assignPayee(payees: any, dataGroups: any) {
    this.availablePayeeGroupsSearchTerm$.next('');
    this.selectedPayeeGroups$.next(payees ? payees : []);
    this.selectPayeeGroupsModalOpened = true;
    this.selectedForAssignAccountGroup = this.dataGroups.find(o => o.id === dataGroups.id);
  }

  assignPayeeGroups(val: any) {
    this.dataGroups.forEach((element, index) => {
      if (this.selectedForAssignAccountGroup && element.id === this.selectedForAssignAccountGroup.id) {
        if (this.dataGroups[index].payees) {
          val.forEach((item: DataGroupWithPayee) => {
            this.dataGroups[index].payees.push(Object.assign({}, item));
          });
        } else {
          this.dataGroups[index].payees = val;
        }
      }
    });
    if (this._userPermissions) {
      this.getAllLinkedPayees(this.dataGroups);
    }
    this.emitRequestObject();
  }

  assignDataGroupsForPayee(val: any) {
    val.forEach((el: any) => {
      const filteredDataGroup = this.dataGroups.find(o => o.id === el.id);
      if (!filteredDataGroup) {
        this.dataGroups.push(...val);
      }
      const findObject = this.dataGroups.find(o => o.id === el.id);
      if (findObject) {
        if (filteredDataGroup && filteredDataGroup.payees && this.selectedForAssignAccountGroupForPayee) {
          findObject.payees.push(this.selectedForAssignAccountGroupForPayee);
        } else {
          findObject.payees = [];
          if (this.selectedForAssignAccountGroupForPayee) {
            findObject.payees.push(this.selectedForAssignAccountGroupForPayee);
          }
        }
      }
    });
    this.getAllLinkedPayees(this.dataGroups);
    this.emitRequestObject();
  }

  addAccountGroupToPayee(payee: any) {
    this.selectedForAssignAccountGroupForPayee = payee;
    this.selectedDataGroupsForPayee$.next(this.getAccountGroupLinked(payee));
    this.selectAccountGroupsForPayeesModalOpened = true;
  }

  getAllLinkedPayees(val: DataGroupWithPayee[]) {
    const payees: string | any[] = [];
    val.forEach((el: any) => {
      if (el['payees']) {
        payees.push(...el['payees']);
      }
    });
    this.allLinkedPayee = payees.filter((v, i, a) => a.findIndex(t => t.id === v.id) === i);
  }

  removeAccountGroup(accountGroup: DataGroup) {
    this.dataGroups = this.dataGroups.filter(item => {
      this.dataGroupForRemove = undefined;
      return item.id !== accountGroup.id;
    });

    this.getAllLinkedPayees(this.dataGroups);
    this.selectedDataGroups$.next(this.dataGroups);
    this.emitRequestObject();
  }

  removedPayeeGroup(payyeGroup: DataGroup, accountGroup: DataGroup) {
    this.dataGroups.forEach((el: DataGroupWithPayee) => {
      if (el.id === accountGroup.id) {
        el.payees = [
          ...el.payees.filter(item => {
            return item !== payyeGroup;
          }),
        ];
      }
    });

    this.getAllLinkedPayees(this.dataGroups);
    this.emitRequestObject();
  }

  removePayeeGroupLinking(payee: DataGroup) {
    this.dataGroups.forEach((el: DataGroupWithPayee) => {
      if (el.payees) {
        this.dataGroupForRemove = undefined;
        el.payees = el.payees.filter(value => {
          return value.id !== payee.id;
        });
      }
    });

    this.getAllLinkedPayees(this.dataGroups);
    if (this.allLinkedPayee.length === 0) {
      this.activeDataGroup = 'account';
    }
    this.emitRequestObject();
  }

  removedAccountGroupFromPayee(payee: DataGroup, account: DataGroup) {
    this.dataGroups.forEach((el: DataGroupWithPayee) => {
      if (el.id === account.id) {
        el.payees = el.payees.filter(value => {
          return value.id !== payee.id;
        });
      }
    });
    this.getAllLinkedPayees(this.dataGroups);
    if (this.allLinkedPayee.length === 0) {
      this.activeDataGroup = 'account';
    }
    this.emitRequestObject();
  }

  onEditPayee() {
    this.preEditDataGroupState = JSON.parse(JSON.stringify(this.dataGroups));
    this.editedFunctionIdEmit.emit(this._userPermissions ? this._userPermissions.id : undefined);
  }

  onCancel() {
    this.editView = false;
    this.dataGroups = JSON.parse(JSON.stringify(this.preEditDataGroupState));
    this.getAllLinkedPayees(this.dataGroups);
    this.selectedDataGroups$.next(this.dataGroups);
    this.editedFunctionIdEmit.emit(undefined);
  }

  emitRequestObject() {
    this.objectForRequest = [];
    this.dataGroups.forEach((accountGroup: DataGroupWithPayee) => {
      if (this._userPermissions) {
        const dataObj = [];
        dataObj.push({ id: accountGroup.id });
        if (accountGroup.payees) {
          accountGroup.payees.forEach((payeeGroup: DataGroup) => {
            dataObj.push({ id: payeeGroup.id });
          });
        }
        this.objectForRequest.push({ functionGroupId: this._userPermissions.id, dataGroupIds: dataObj });
      }
    });
    if (this.objectForRequest.length === 0 && this._userPermissions) {
      this.objectForRequest.push({ functionGroupId: this._userPermissions.id, dataGroupIds: [] });
    }
    if (this.objectForRequest.length !== 0 && this.selfApprovalPolicies && this.selfApprovalPolicies.length) {
      this.objectForRequest.forEach((el: any) => {
        el.selfApprovalPolicies = this.selfApprovalPolicies?.filter(
          (item: SelfApprovalPolicies) => !this.checkForUnsetPolicies(item),
        );
      });
    }
    this.requestObjectEmit.emit(this.objectForRequest);
    if (this._userPermissions && this.dataGroups && this.preEditDataGroupState) {
      this.changesMadedEmit.emit({ [this._userPermissions.id]: this.checkChanges() });
    }
  }

  onSave() {
    this.emitRequestObject();
  }

  checkForUnsetPolicies(el: SelfApprovalPolicies) {
    return (
      (el.canSelfApprove && this.selfApproveDefaultBehavior === 'enable' && !el.bounds?.length) ||
      (!el.canSelfApprove && this.selfApproveDefaultBehavior === 'disable' && !el.bounds?.length)
    );
  }

  checkChanges(): boolean {
    if (this.dataGroups.length !== this.preEditDataGroupState.length) {
      return false;
    } else {
      for (let i = 0; i < this.dataGroups.length; i++) {
        const savedObject = this.preEditDataGroupState.find(el => el.id === this.dataGroups[i].id);
        if (!savedObject) {
          return false;
        } else if (this.checkPayeesForChanges(savedObject.payees, this.dataGroups[i].payees)) {
          return false;
        }
      }
    }

    const resultA = this.selfApprovalPoliciesHistory?.filter(
      elm => !this.selfApprovalPolicies?.map(elm => JSON.stringify(elm)).includes(JSON.stringify(elm)),
    );
    const resultB = this.selfApprovalPolicies?.filter(
      elm => !this.selfApprovalPoliciesHistory?.map(elm => JSON.stringify(elm)).includes(JSON.stringify(elm)),
    );

    if (resultA?.length || resultB?.length) {
      return false;
    }
    return true;
  }

  checkPayeesForChanges(savedObjects: DataGroup[], dataGroupPayeeObjects: DataGroup[]): boolean {
    if (
      (!savedObjects && dataGroupPayeeObjects && dataGroupPayeeObjects.length) ||
      (savedObjects && savedObjects.length && !dataGroupPayeeObjects)
    ) {
      return true;
    }
    if (savedObjects && dataGroupPayeeObjects && savedObjects.length !== dataGroupPayeeObjects.length) {
      return true;
    }
    if (savedObjects && dataGroupPayeeObjects && savedObjects.length === dataGroupPayeeObjects.length) {
      for (let j = 0; j < dataGroupPayeeObjects.length; j++) {
        const savedObjectPayee = savedObjects.find(el => el.id === dataGroupPayeeObjects[j].id);
        if (!savedObjectPayee) {
          return true;
        }
      }
    }
    return false;
  }

  onOpenUnassignModal(dataGroup: DataGroupWithPayee) {
    this.dataGroupForRemove = dataGroup;
  }

  openPayeeGroupDetail(payee: DataGroup) {
    // @ts-ignore
    this.payeeIds = payee.items;
    this.payeeGroupName = payee.name;
    this.payeeDetailModal = true;
  }

  openAccountGroupDetail(account: DataGroup) {
    // @ts-ignore
    this.accountIds = account.items;
    this.accountGroupName = account.name;
    this.accountDetailModal = true;
  }

  /**
   * Return flag of financial BF and bound support for self approve
   * @param item - BF privileges
   */
  checkIfSelfApproveLimitsSupport(item: BusinessFunctionPrivileges) {
    let countOfSupportLimit = 0;
    for (const property in item) {
      if (item[property].supportsLimits) {
        countOfSupportLimit++;
      }
    }
    return item.approve && item.approve.assigned && countOfSupportLimit > 0;
  }

  /**
   * Return flag of self approve supported
   * @param item - BF privileges
   */
  checkIfSelfApprove(item: BusinessFunctionPrivileges) {
    return item.approve && item.approve.assigned;
  }

  /**
   * Return flag of present any self approval item in group of BF
   * @param items - BF privileges in Group
   */
  checkIfGroupHasSelfApprove(items: BusinessFunction[]) {
    let appovePrivelegCount = 0;
    items.forEach((el: any) => {
      if (this.checkIfSelfApprove(el.privileges)) {
        appovePrivelegCount++;
      }
    });

    return appovePrivelegCount > 0;
  }

  /**
   * Return flag of state of self approval
   * @param name - name of BF
   */
  checkIfSelfApprovalPoliciesSetted(name: string) {
    return this.selfApprovalPolicies?.find((el: SelfApprovalPolicies) => el.businessFunctionName === name)
      ?.canSelfApprove;
  }

  /**
   * Return flag of disabled of self approval
   * @param name - name of BF
   */
  checkIfSelfApprovalDisabled(name: string) {
    if (this.selfApprovalPolicies?.find((el: SelfApprovalPolicies) => el.businessFunctionName === name)) {
      return !this.selfApprovalPolicies?.find((el: SelfApprovalPolicies) => el.businessFunctionName === name)
        ?.canSelfApprove;
    }
    return false;
  }

  /**
   * Return bounds for self approve
   * @param name - name of BF
   */
  getBoundsForSelfApprove(name: string) {
    return this.selfApprovalPolicies?.find((el: SelfApprovalPolicies) => el.businessFunctionName === name)?.bounds;
  }

  /**
   * Open modal for edit self Approve
   * @param bf -  BF
   */
  editSelfApprove(bf: BusinessFunction) {
    this.selfApproveItemData = this.selfApprovalPolicies?.find(
      (el: SelfApprovalPolicies) => el.businessFunctionName === bf.name,
    );
    this.typeOfSelfApproveAction = 'edit';
    this.selectedBFForSelfApprove = bf;
  }

  /**
   * Open modal for enable self Approve
   * @param bf -  BF
   */
  enableSelfApprove(bf: BusinessFunction) {
    this.selfApproveItemData = this.selfApprovalPolicies?.find(
      (el: SelfApprovalPolicies) => el.businessFunctionName === bf.name,
    );
    this.typeOfSelfApproveAction = 'enable';
    this.selectedBFForSelfApprove = bf;
  }

  /**
   * Changed response object when self approval item changed
   * @param event -  SelfApprovalPolicies
   */
  onSelfApproveChanges(event: SelfApprovalPolicies) {
    this.selfApprovalPolicies = this.selfApprovalPolicies?.filter((obj: SelfApprovalPolicies) => {
      return obj.businessFunctionName !== event.businessFunctionName;
    });
    if (this.selfApprovalPolicies && this.selfApprovalPolicies.length) {
      this.selfApprovalPolicies.push(event);
    } else {
      this.selfApprovalPolicies = [];
      this.selfApprovalPolicies.push(event);
    }
    this.emitRequestObject();
  }
}
